.accountUserContainer{
    width: 100%;
    height: 56px;
    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: 20px;
    background-color: #fff;
}

.accountUserNumeration{
    width: 26px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 13px;
    color: #3c3c3c;
    background-color: #efefef;
    border-radius: 14px 0 0 14px;
}

.accountUserInfo{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.accountUserName{
    color: #212121;
    font-size: 16px;
}

#overPlace{
    color: rgb(174 179 192);
    font-size: 15px;
}